import React from "react";
import { Link } from "react-router-dom";
import Circle from "../images/Circle.jpg";

const IndexComp = () => (
  <div className="container" style={{ height: 1000 }}>
    <img
      alt="Khang"
      src={Circle}
      style={{
        borderRadius: "50%",
        width: 250,
        position: "absolute",
        left: "50%",
        marginLeft: -125,
        marginTop: 20
      }}
    />
    <div
      style={{
        position: "absolute",
        textAlign: "center",
        top: 270,
        width: "25%",
        left: "50%",
        marginLeft: "-12.5%",
        fontSize: 28,
        fontWeight: "bold",
        padding: 0,
        lineHeight: "100%"
      }}
    >
      This is Me
    </div>
    <div
      style={{
        position: "absolute",
        textAlign: "center",
        top: 350,
        width: "75%",
        left: "50%",
        marginLeft: "-37.5%",
        fontSize: 16
      }}
    >
      Hi, my name is Khang and this is my website. It's made using React. You
      might be wondering why the site looks like trash, and that's just because
      you don't understand art. I'm just kidding, it's probably because I have
      no idea what I'm doing.
      <div style={{ padding: 20 }} />
      Now click this button to find out more about me.
      <div style={{ padding: 5 }} />
      <div className="cta__inner" style={{ textAlign: "center" }}>
        <Link
          className="button button__accent"
          to="/Biography"
          style={{ fontSize: 20 }}
        >
          Who am I?
        </Link>
      </div>
    </div>
  </div>
);
export default IndexComp;
