import React from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import Footer from "./Footer";
import "../css/style.min.css";
import "../index.css";

class Navigation extends React.Component {
  state = {
    Home: "Home",
    Biography: "Biography",
    Projects: "Projects",
    Journey: "Journey"
  };

  onUpdate = () => {
    if (window.location.pathname == "/") {
      this.setState({
        Home: <b>Home</b>
      });
    } else {
      this.setState({
        Home: "Home"
      });
    }

    if (window.location.pathname == "/Biography") {
      this.setState({
        Biography: <b>Biography</b>
      });
    } else {
      this.setState({
        Biography: "Biography"
      });
    }

    if (window.location.pathname == "/Projects") {
      this.setState({
        Projects: <b>Projects</b>
      });
    } else {
      this.setState({
        Projects: "Projects"
      });
    }

    if (window.location.pathname == "/Journey") {
      this.setState({
        Journey: <b>Journey</b>
      });
    } else {
      this.setState({
        Journey: "Journey"
      });
    }
  };

  componentDidMount() {
    this.onUpdate();
  }

  render() {
    return (
      <div>
        <div className="navbar navbar--extended">
          <nav className="nav__mobile" />
          <div className="container">
            <div className="navbar__inner">
              <Link className="navbar__logo" to="/">
                Khang Nguyen
              </Link>

              <nav className="navbar__menu">
                <ul>
                  <li>
                    <a>
                      <Link to="/" onClick={this.onUpdate}>
                        {this.state.Home}
                      </Link>
                    </a>
                  </li>
                  <li>
                    <a>
                      <Link to="/Biography" onClick={this.onUpdate}>
                        {this.state.Biography}
                      </Link>
                    </a>
                  </li>
                  <li>
                    <a>
                      <Link to="/Projects" onClick={this.onUpdate}>
                        {this.state.Projects}
                      </Link>
                    </a>
                  </li>
                  <li>
                    <a>
                      <Link to="/Journey" onClick={this.onUpdate}>
                        {this.state.Journey}
                      </Link>
                    </a>
                  </li>
                </ul>
              </nav>
              <div className="navbar__menu-mob">
                <a href="" id="toggle">
                  <svg
                    role="img"
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 448 512"
                  >
                    <path
                      fill="currentColor"
                      d="M16 132h416c8.837 0 16-7.163 16-16V76c0-8.837-7.163-16-16-16H16C7.163 60 0 67.163 0 76v40c0 8.837 7.163 16 16 16zm0 160h416c8.837 0 16-7.163 16-16v-40c0-8.837-7.163-16-16-16H16c-8.837 0-16 7.163-16 16v40c0 8.837 7.163 16 16 16zm0 160h416c8.837 0 16-7.163 16-16v-40c0-8.837-7.163-16-16-16H16c-8.837 0-16 7.163-16 16v40c0 8.837 7.163 16 16 16z"
                      className=""
                    />
                  </svg>
                </a>
              </div>
            </div>
          </div>
        </div>
        <div>
          <div className="page__header">
            <div className="hero__overlay hero__overlay--gradient" />
            <div className="hero__mask" />
            <div className="page__header__inner" />
          </div>
        </div>
      </div>
    );
  }
}
export default Navigation;
