import React from "react";
import { Link } from "react-router-dom";
import Snake from "../images/Snake.jpg";
import Instagram from "../images/Instagram.png";
import Github from "../images/Github.png";
import Linkedin from "../images/Linkedin.jpg";

const IndexComp = () => (
  <div
    style={{
      height: "100%",
      position: "relative",
      marginLeft: "3%",
      marginTop: "3%"
    }}
  >
    <div className="bio" style={{ position: "absolute", float: "left" }}>
      <img
        alt="Khang Holding a Snake"
        src={Snake}
        style={{
          width: "100%",
          border: "solid",
          borderColor: "black",
          borderWidth: 5,
          lineHeight: "100%"
        }}
      />
      <br />
      <b className="title" style={{ color: "black" }}>
        {" "}
        Summary
      </b>
      <br />
      <p style={{ lineHeight: "100%" }}>
        <b>Name:</b> Khang Nguyen
      </p>
      <p style={{ lineHeight: "100%" }}>
        <b>Education:</b> Software Engineering class of 2021
      </p>
      <p style={{ lineHeight: "100%" }}>
        <b>Institution:</b> University of Ottawa
      </p>
      <p style={{ lineHeight: "100%" }}>
        <b>Specialty:</b>
        <br />
        Java(Object Oriented)
        <br />
        Javascript(NodeJS)
        <br />C<br />
        pgSQL
        <br />
        YANG
        <br />
        JSON
        <br />
        BASH
        <br />
        C++
        <br />
        AutoHotKey
        <br />
        Selenium(Java)
        <br />
        Assembly machine code
        <br />
        Python coding
        <br />
        gRPC and REST protocols
        <br />
        service router configurations
      </p>
      <p style={{ lineHeight: "100%" }}>
        <b>Software Utilities:</b>
        <br />
        Docker
        <br />
        Vagrant
        <br />
        npm
        <br />
        Jenkins
        <br />
        Git (GitLab)
        <br />
        Confluence
        <br />
        JIRA
      </p>
    </div>
    <div
      className="bio2"
      style={{ position: "absolute", top: "3%", float: "right" }}
    >
      <b className="title" style={{ color: "black" }}>
        {" "}
        About me
      </b>
      <br />
      <p style={{ lineHeight: "110%" }}>
        Hello, I'm Khang the magnificent creator of this website. I'm using this
        website as a way to learn REACT and a little bit about front end
        development. My main area of expertise have mostly been in automation,
        infrastructure developement and cyber security. I like riding
        motorcycles and playing with cats and dogs (dogs are better and anyone
        that thinks otherwise is wrong). I enjoy doing projects on my free time
        when I'm not with friends or playing games. That's basically all I have
        to say, The summary was the main part of this page (My
        <Link to="/Resume" style={{ color: "red" }}>
          {" "}
          resume{" "}
        </Link>
        is also in the footer for anyone curious about my competence).
      </p>
      <br />
      <p style={{ marginTop: "5%", lineHeight: "110%", fontSize: "150%" }}>
        Follow me on these platforms
      </p>
      <div className="social">
        <a href="https://github.com/KhangNguyen3000" target="_blank">
          <img
            alt="Github"
            className="social"
            src={Github}
            style={{ float: "left" }}
          />
        </a>
        <a href="https://www.instagram.com/khangaroooo/" target="_blank">
          <img
            alt="Instagram"
            className="social"
            src={Instagram}
            style={{ float: "right" }}
          />
        </a>
        <a href="https://www.linkedin.com/in/khanghnguyen1999/" target="_blank">
          <img
            alt="Linkedin"
            className="social"
            src={Linkedin}
            style={{
              display: "block",
              marginRight: "auto",
              marginLeft: "auto"
            }}
          />
        </a>
      </div>
    </div>
  </div>
);
export default IndexComp;
