import React from "react";
import { Link } from "react-router-dom";

const Footer = () => (
  <div className="footer footer--dark">
    <div className="container">
      <div className="footer__inner">
        <div>
          <Link
            to="/"
            className="footer__textLogo"
            style={{ textAlign: "center", fontSize: "1.25rem", padding: 0 }}
          >
            Created by Khang
          </Link>
          <br />
          <div className="cta__inner" style={{ textAlign: "center" }}>
            <Link
              className="button button__accent"
              to="/Resume"
              style={{ fontSize: 16 }}
            >
              Resume
            </Link>
          </div>
        </div>
        <div className="footer__data">
          <div
            className="footer__data__item"
            style={{ textAlign: "center", width: "100%", fontSize: "1.rem" }}
          >
            <div className="footer__row">pls no stealarino</div>
          </div>
          <div className="footer__data__item" style={{ fontSize: "1.25em" }}>
            <div className="footer__row">
              <Link to="/" href="https://undraw.co" className="footer__link">
                Home
              </Link>
            </div>
            <div className="footer__row">
              <Link
                to="/Biography"
                href="https://undraw.co"
                className="footer__link"
              >
                Biography
              </Link>
            </div>
            <div className="footer__row">
              <Link
                to="/Projects"
                href="https://undraw.co"
                className="footer__link"
              >
                Projects
              </Link>
            </div>
            <div className="footer__row">
              <Link
                to="/Journey"
                href="https://undraw.co"
                className="footer__link"
              >
                Journey
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
);

export default Footer;
