import React from "react";
import { BrowserRouter, Route, Link, Switch } from "react-router-dom";
import Footer from "../components/Footer";
import Navigation from "../components/Navigation";
import Student1 from "../images/Student1.png";
import Student2 from "../images/Student2.png";
import Student3 from "../images/Student3.jpg";

export default () => (
  <div
    className="bodyContainer"
    style={{
      display: "flex",
      flexDirection: "column",
      alignItems: "stretch",
      justifyContent: "space-between"
    }}
  >
    <div style={{ width: "100%" }}>
      <Navigation />
      <div>
        <img
          src={Student1}
          style={{
            width: "50%",
            marginLeft: "auto",
            marginRight: "auto",
            display: "block"
          }}
        />
        <img
          src={Student2}
          style={{
            width: "50%",
            marginLeft: "auto",
            marginRight: "auto",
            display: "block"
          }}
        />
        <img
          src={Student3}
          style={{
            width: "50%",
            marginLeft: "auto",
            marginRight: "auto",
            display: "block"
          }}
        />
      </div>
    </div>
    <Footer style={{ alignSelf: "flex-end" }} />
  </div>
);
